import EvanRadio from '@/components/evan-radio/evan-radio.vue';
import sPopup from '@/components/s-popup/index.vue';
export default {
	components: {
		EvanRadio,
		sPopup
	},
	data() {
		return {
			check:true,
			check_1:false,
			visible:false,
			baseValue: 'base1',
			order:'order1',
			rentData: [
				{ image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price: 36.00,number:1},
				{ image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price: 36.00,number:1},
				{ image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price: 36.00,number:1},
				{ image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price: 36.00,number:1}
			],
			message:'请下午5点后帮我送到',
			priceInfo:[
				{left:'商品金额',right:'￥137.4'},
				{left:'商品数量',right:'x3'},
				{left:'配送费',right:'￥0.0'},
				{left:'租贷押金',right:'￥50.0'},
				{left:'总计',right:'￥187.4'},
				],
			number:2,
			allPrice:187.4
		};
	},
	methods: {
		radioChange: function(evt) {
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].value === evt.target.value) {
					this.current = i;
					break;
				}
			}
		},
		toAddress(){
			uni.navigateTo({
				url:"./myAdress"
			})
		},
		toAccount(){
			this.visible = true
		},
		comfirm(){
			uni.navigateTo({
				url:'./success'
			})
		}
	}
};
